<template>
  <div id="changeProfile">
    <head-vue title="Change Profile" goBack="true"></head-vue>
    <div class="connect">
      <div class="top" ref="top" :style="{backgroundImage:'url('+headpic+')'}">
        <div class="icon" @click="getPicture">
          <img :src="headpic" alt="" ref="headSrc">
          <img src="../../assets/me/photo@3x.png" alt="">
        </div>
        <div class="traingle">
          <img src="../../assets/me/stiangle@3x.png" alt="">
          <img src="../../assets/me/triangle_white.png" alt="">
        </div>
      </div>
      <ul>
        <li>
          <span>Name</span>
          <div class="li_right" @click="$router.push({path: '/Me/changename'})">
            <span>{{firstname}}</span>
            <img src="../../assets/me/next@3x.png" alt="">
          </div>
        </li>
        <li>
          <span>Surname</span>
          <div class="li_right"  @click="$router.push({path: '/Me/changesurname'})">
            <span>{{surname}}</span>
            <img src="../../assets/me/next@3x.png" alt="">
          </div>
        </li>
        <li>
          <span>Email</span>
          <div class="li_right">
            <span>{{email}}</span>
          </div>
        </li>
      </ul>
    </div>
    <actionSheet v-show="actionSheet" setHeight="200">
      <div slot="main">
        <ul class="changePic">
          <li>Change pic</li>
          <li @click="actionMode(1)">Take a photo</li>
          <li @click="actionMode(2)">Select from the phone album</li>
          <li @click="actionMode(3)">Cancel</li>
        </ul>
      </div>
    </actionSheet>
    <loading></loading>
  </div>
</template>
<script>
import head from '../../components/common/head'
import loading from '../../components/loading/index'
import actionSheet from '../../components/common/actionSheet'
import { mapActions, mapGetters } from 'vuex'
import { getStore } from '../../utils'

import {
  localPhotoRequest,
  takePhotoRequest
} from '../../api/method'
import {
  modifyAvatarSendedResponse,
  modifyUserInformationResponse
} from '../../api/getData'
export default {
  data () {
    return {
      email: 'TomCruise@gmail.com', // 邮箱地址
      headpic: null
    }
  },
  components: {
    'head-vue': head,
    actionSheet,
    loading
  },
  computed: {
    ...mapGetters(['getActionsheet', 'getuserName', 'getsurName']),
    actionSheet () {
      return this.getActionsheet
    },
    firstname () {
      return this.getuserName
    },
    surname () {
      return this.getsurName
    }
  },
  created () {
    this.headpic = JSON.parse(getStore('Userinfo')).headPic
    console.log(this.headpic)
  },
  mounted () {
    modifyAvatarSendedResponse(this.NAVIGATOR, this)
    modifyUserInformationResponse(this.NAVIGATOR, this)
    // this.$refs.top.style.background = 'url('+ this.headpic + ')'
    // if (this.$root.ISDEBUG) {
    //   window.modifyUserInformationResponse(true, '{"headPic":"http:\/\/file.startai.com.cn:3108\/group1\/M00\/00\/0F\/Zy1orFubejuEB380AAAAAAAAAAA558.jpg"}')
    // }
  },
  methods: {
    ...mapActions([
      'showActionsheet',
      'showLoading',
      'setHeadpic'
    ]),
    getPicture () {
      this.showActionsheet()
      // this.$store.dispatch('showActionsheet')
      console.log(this.getActionsheet)
    },
    actionMode (num) {
      switch (num) {
        case 1:
          takePhotoRequest(this.NAVIGATOR, this)
          this.showActionsheet()
          break
        case 2:
          localPhotoRequest(this.NAVIGATOR, this)
          this.showActionsheet()
          break
        case 3:
          this.showActionsheet()
          break
        default:
          break
      }
    }
  }
}
</script>
<style lang="less" scoped>
#changeProfile{
  .fs;
  z-index: 999;
  background: white;
  .connect{
    .wh(100%,100%);
    .top{
      .wh(100%,400px);
      position: relative;
      .mask{
        .wh(100%, 100%);
        position: absolute;
        top: 0;
        left: 0;
        // background: rgba(238, 190, 68, 0.562);
        z-index: 10;
      }
      .icon{
        .wh(200px, 200px);
        position: absolute;
        top: 66px;
        left: 50%;
        margin-left: -100px;
        z-index: 11;
        border-radius: 50%;
        img:nth-child(1){
          .wh(100%, 100%);
          border-radius: 50%;
        }
        img:nth-child(2){
          .wh(60px, 48px);
          position: absolute;
          bottom: 0;
          right: -5px;
        }
      }
      .traingle{
        position: absolute;
        bottom: -5px;
        z-index: 12;
        .wh(100%, 75px);
        img:nth-child(1) {
          .wh(50%,100%);
          position: absolute;
          bottom: 0;
          right: 0;
          z-index: 15;
        }
        img:nth-child(2) {
          .wh(100%,100%);
          position: absolute;
          bottom: 0px;
          left: 0;
          z-index: 15;
        }
      }
    }
    .top{
      background: url(../../assets/me/header@3x.png) no-repeat;
      background-size: 100% 100%;
      background-repeat: no-repeat;
    }
    .top:after{
        content: "";
        width:100%;
        height:98%;
        position: absolute;
        left:0;
        top:0;
        background: inherit;
        filter: blur(5px);
        z-index: 2;
      }
    ul{
      .wh(100%,100%);
      margin-top: 40px;
      li{
        .wh-l(100%, 100px);
        display: flex;
        padding: 0 30px;
        border-bottom: 1px solid #eeeeee;
        font-size: 23px;
        justify-content: space-between;
        .li_right{
          flex: 1;
          color: #cdcdcd;
          display: flex;
          justify-content: flex-end;
          span{
            margin-right: 36px;
          }
          img{
            .wh(14px,24px);
            align-self: center
          }
        }
      }
    }
  }
  .changePic{
    .wh(100%,auto);
    li{
      .wh-l(100%,100px);
      font-size: 31px;
      color: #575757;
      border-bottom: 1px solid #eeeeee;
    }
    li:nth-child(4){
        border-bottom: 0;
    }
    li:nth-child(1) {
      font-weight: 900;
    }
  }
}
</style>



